
import { defineComponent } from "vue";

export default defineComponent({
  name: "Description",
  props: {
    descName: {
      type: String,
      required: true,
    },
    descInfo: {
      type: String,
      required: true,
    },
    descDate: {
      type: String,
      required: true,
    },
    spaceMedia: {
      type: Array,
      required: true,
    },
    dateIcon: {
      type: String,
      required: true,
    },
  },
  methods: {
    displayDate(value) {
      if (value == null || value == "") return "";

      return new Date(value).toLocaleString("en-GB", {
        day: "2-digit",
        month: "long",
        year: "numeric",
      });
    },
  },
});
