
import { mapMutations, mapState } from "vuex";
import IgButton from "@/components/baseUI/IgButton.vue";
import { defineComponent } from "vue";
import axios from "axios";
import { emitter } from "@/main";
import {
  TransitionRoot,
  TransitionChild,
  Dialog,
  DialogOverlay,
} from "@headlessui/vue";
export default defineComponent({
  name: "AddPost",
  props: {
    WithdrawOpen: {
      type: Boolean,
      required: true,
    },
    ModalName: {
      type: String,
      required: true,
    },
    backgroundColor: {
      type: String,
      required: true,
    },
    fontBold: {
      type: String,
      required: true,
    },
  },

  data() {
    return {
      description: "",
      descriptionErrorMessage: "",
      imageErrorMessage: "",
      image: "",
      dataImage: "",
    };
  },
  components: {
    TransitionRoot,
    TransitionChild,
    Dialog,
    DialogOverlay,
    IgButton,
  },
  computed: {
    ...mapState(["apiUrl"]),
    ...mapState(["ADDPOST"]),
  },
  methods: {
    ...mapMutations(["TOGGLE_ADD_POST"]),
    AddPost() {
      this.TOGGLE_ADD_POST();
    },
    imageLoad(event) {
      if (event.target.files && event.target.files[0]) {
        this.image = event.target.files[0];
        var reader = new FileReader();

        reader.readAsDataURL(event.target.files[0]); // read file as data url

        reader.onload = (e) => {
          this.dataImage =
            e.target && e.target.result ? e.target.result.toString() : "";
        };
      }
    },
    removeImage() {
      this.image = "";
      this.dataImage = "";
    },
    async submitPost() {
      this.descriptionErrorMessage = "";
      if (this.description != "") {
        try {
          let fromData = new FormData();
          fromData.append("space_uuid", this.$route.params.id.toString());
          fromData.append("description", this.description);
          if (this.image != "") {
            fromData.append("image", this.image);
          }
          var response = await axios.post(
            this.apiUrl + "/space/" + this.$route.params.id + "/post",
            fromData,
            {
              headers: {
                "Content-Type": "multipart/form-data",
                authorization: "Bearer " + localStorage.getItem("access_token"),
              },
            }
          );
          if (response.data) {
            this.image = "";
            this.dataImage = "";
            this.description = "";
            emitter.emit("addPost", response.data);
            this.AddPost();
          }
        } catch (error) {
          if (error && error.response) {
            if (error.response.data) {
              if (error.response.data.description) {
                this.descriptionErrorMessage =
                  error.response.data.description[0];
              }
              if (error.response.data.image) {
                this.imageErrorMessage = error.response.data.image[0];
              }
            }
          }
        }
      } else {
        this.descriptionErrorMessage = "Description field is required";
      }
    },
  },
});
