<template>
  <div class="p-16 md:p-30 bg-black-default rounded-large mb-6">
    <div class="flex">
      <div role="img" class="mr-16">
        <img
          v-if="data.member_profile_image != null"
          :src="data.member_profile_image"
          style="max-width: 3.2rem; border-radius: 15px"
        />
        <img
          v-else
          src="@/assets/images/user.png"
          style="max-width: 3.2rem; border-radius: 15px"
        />
      </div>
      <div class="my-auto">
        <h5 class="text-white font-bold">{{ data.member_name }}</h5>
        <p class="text-sm text-gray-200 mt-2">
          {{ displayDateTime(data.created) }}
        </p>
      </div>
    </div>
    <div
      v-if="data.image != null"
      class="my-6 post-image-block"
      :style="{ backgroundImage: 'url(' + data.image + ')' }"
    ></div>
    <div class="mt-3">
      <p class="text-gray-200 mb-2">
        {{ data.description }}
      </p>
      <div class="flex justify-between">
        <div class="flex">
          <div role="img" class="mr-16 flex items-center">
            <img src="@/assets/images/spaces/like.svg" />
            <span class="text-gray text-sm ml-2">{{ like }}</span>
          </div>
          <div role="img" class="mr-16 flex items-center text-sm text-gray-200">
            <img src="@/assets/images/spaces/dislike.svg" />
            <span class="text-gray text-sm ml-2">{{ dislike }}</span>
          </div>
          <div class="flex items-center">
            <p class="text-sm text-gray mr-1">
              {{ data.comments.length + " " + $t("post.comments") }}
            </p>
            <!-- <span>
              <svg
                width="3"
                height="3"
                viewBox="0 0 3 3"
                fill="none"
                xmlns="http://www.w3.org/2000/svg"
              >
                <circle cx="1.5" cy="1.5" r="1.5" fill="#4E5163" />
              </svg>
            </span> -->
          </div>

          <!-- <button class="text-sm text-white ml-2 hide_el sm:block">
            {{ $t("post.see_all") }}
          </button> -->
        </div>

        <div class="text-sm text-white flex items-center hide_el sm:block">
          <button role="img" class="flex">
            <img src="@/assets/images/spaces/edit.svg" />
            <span class="ml-2"> Write reply</span>
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent } from "vue";

export default defineComponent({
  name: "CreatePost",
  props: {
    data: {
      type: Object,
      required: true,
    },
  },
  data() {
    return {
      like: 0,
      dislike: 0,
    };
  },
  methods: {
    displayDateTime(value) {
      if (value == null || value == "") return "";

      return new Date(value).toLocaleString("en-GB", {
        day: "2-digit",
        month: "long",
        year: "numeric",
        minute: "2-digit",
        second: "2-digit",
      });
    },
  },
  created() {
    this.like = 0;
    this.dislike = 0;
    if (this.data.like_dislikes) {
      this.data.like_dislikes.forEach((element) => {
        if (element.like) this.like++;
        if (element.dislike) this.dislike++;
      });
    }
  },
});
</script>
<style lang="scss" scoped>
.post-image-block {
  height: 18rem;
  background-position: center;
  background-size: cover;
  border-radius: 15px;
}
</style>
