      <path
        fill-rule="evenodd"
        clip-rule="evenodd"
        d="M22 16C21.2636 16 20.6667 16.597 20.6667 17.3333V20.6667H17.3333C16.597 20.6667 16 21.2636 16 22C16 22.7364 16.597 23.3333 17.3333 23.3333H20.6667V26.6667C20.6667 27.403 21.2636 28 22 28C22.7364 28 23.3333 27.403 23.3333 26.6667V23.3333H26.6667C27.403 23.3333 28 22.7364 28 22C28 21.2636 27.403 20.6667 26.6667 20.6667H23.3333V17.3333C23.3333 16.597 22.7364 16 22 16Z"
        fill="white"
      />
    </svg>
  </button>
  <!-- @close="closeModal" -->
  <TransitionRoot appear :show="ADDPOST" as="template">
    <Dialog as="div" class="rounded-lg">
      <div class="fixed inset-0 overflow-y-auto" style="z-index: 5000">
        <div class="min-h-screen px-4 text-center">
          <TransitionChild
            as="template"
            enter="duration-300 ease-out"
            enter-from="opacity-0 scale-95"
            enter-to="opacity-100 scale-100"
            leave="duration-200 ease-in"
            leave-from="opacity-100 scale-100"
            leave-to="opacity-0 scale-95"
          >
            <DialogOverlay class="fixed inset-0 bg-black opacity-80" />
          </TransitionChild>

          <span class="inline-block h-screen align-middle" aria-hidden="true">
            &#8203;
          </span>

          <TransitionChild
            as="template"
            enter="duration-300 ease-out"
            enter-from="opacity-0 scale-95"
            enter-to="opacity-100 scale-100"
            leave="duration-200 ease-in"
            leave-from="opacity-100 scale-100"
            leave-to="opacity-0 scale-95"
          >
            <div
              class="
                relative
                inline-block
                p-6
                my-8
                overflow-hidden
                text-left
                align-middle
                transition-all
                transform
                rounded-2xl
                bg-gray-400
              "
              style="z-index: 1000; width: 507px"
            >
              <div class="flex flex-col">
                <!-- // header -->
                <div class="flex justify-between">
                  <h3 class="text-xl font-bold text-white">Add new post</h3>
                  <button role="button" @click="AddPost">
                    <svg
                      width="16"
                      height="16"
                      viewBox="0 0 16 16"
                      fill="none"
                      xmlns="http://www.w3.org/2000/svg"
                    >
                      <path
                        d="M15.6548 1.40965L14.558 0.325903C14.2613 0.029129 13.7709 0.029129 13.4613 0.325903L7.99029 5.79674L2.41623 0.222677C2.11932 -0.0742258 1.629 -0.0742258 1.31945 0.222677L0.222677 1.31945C-0.0742258 1.61623 -0.0742258 2.10655 0.222677 2.41623L5.78397 7.97752L0.325903 13.4614C0.029129 13.7582 0.029129 14.2485 0.325903 14.5582L1.42268 15.6549C1.71945 15.9517 2.20977 15.9517 2.51945 15.6549L7.99029 10.184L13.4613 15.6549C13.758 15.9517 14.2484 15.9517 14.558 15.6549L15.6548 14.5582C15.9516 14.2614 15.9516 13.7711 15.6548 13.4614L10.1709 7.99042L15.6419 2.51958C15.9516 2.20965 15.9516 1.71932 15.6548 1.40965Z"
                        fill="white"
                      />
                    </svg>
                  </button>
                </div>

                <!-- add photo to the post -->
                <div
                  class="flex justify-center items-center flex-col mt-30"
                  style="
                  position: relative
                    width: 444px;
                    height: 224px;
                    background-color: #1b1d25;
                    border-radius: 14px;
                    overflow: hidden;
                  "
                >
                  <div role="img" class="mb-40">
                    <img
                      v-if="dataImage != ''"
                      :src="dataImage"
                      style="max-width: 100%; max-height: 100%"
                    />
                    <svg
                      v-else
                      width="103"
                      height="102"
                      viewBox="0 0 103 102"
                      fill="none"
                      xmlns="http://www.w3.org/2000/svg"
                    >
                      <path
                        fill-rule="evenodd"
                        clip-rule="evenodd"
                        d="M89.75 13.6016H13.25C6.22054 13.6016 0.5 19.3221 0.5 26.3516V77.3516C0.5 77.4851 0.518598 77.6145 0.537268 77.7444C0.551893 77.8461 0.566562 77.9481 0.572372 78.0527C0.512451 78.5624 0.538132 79.0768 0.724899 79.5616C1.77469 85.5374 6.97694 90.1016 13.25 90.1016H89.75C96.7795 90.1016 102.5 84.381 102.5 77.3516V26.3516C102.5 19.3221 96.7795 13.6016 89.75 13.6016ZM13.2499 22.1018H89.7499C92.0915 22.1018 93.9996 24.01 93.9996 26.3516V60.0927L71.6451 37.7374C68.7378 34.83 64.0079 34.83 61.1052 37.7374L40.8752 57.9674L35.5196 52.6126C32.613 49.7053 27.8824 49.7053 24.9797 52.6126L9.00015 68.5922V26.3516C9.00015 24.01 10.9083 22.1018 13.2499 22.1018ZM21.7503 43.3513C26.4444 43.3513 30.2497 39.5459 30.2497 34.8518C30.2497 30.1569 26.4444 26.3516 21.7503 26.3516C17.0554 26.3516 13.25 30.1569 13.25 34.8518C13.25 39.5459 17.0554 43.3513 21.7503 43.3513Z"
                        fill="#272832"
                      />
                    </svg>
                  </div>
                  <div class="image-btn">
                    <input
                      v-if="dataImage == ''"
                      type="file"
                      id="image_file"
                      ref="image_file"
                      class="file-input"
                      @change="imageLoad($event)"
                    />
                    <IgButton
                      v-else
                      class="ig-auto px-3 ig-small ig-font-small"
                      buttonName="remove"
                      textColor="ig-black-text"
                      backgroundColor="ig-white"
                      @click="removeImage()"
                    />
                  </div>
                </div>
                <div>
                  <small class="color-red" v-if="imageErrorMessage != ''">{{
                    imageErrorMessage
                  }}</small>
                </div>

                <!-- Add Description  -->
                <div
                  class="mt-6"
                  style="
                    border-radius: 9px;
                    width: 443px;
                    height: 176px;
                    background-color: #14161e;
                  "
                >
                  <textarea
                    class="
                      w-full
                      h-full
                      bg-gray-400
                      outline-none
                      text-white
                      p-4
                      border border-gray-100
                      rounded-large
                    "
                    :class="descriptionErrorMessage != '' ? 'border-red' : ''"
                    id="descriptions"
                    cols="30"
                    rows="10"
                    placeholder="Write descriptions"
                    v-model="description"
                  ></textarea>
                </div>
                <div>
                  <small
                    class="color-red"
                    v-if="descriptionErrorMessage != ''"
                    >{{ descriptionErrorMessage }}</small
                  >
                </div>
                <div class="mt-4">
                  <IgButton
                    class="ig-full"
                    buttonName="post"
                    textColor="ig-white-text"
                    backgroundColor="ig-purple"
                    @click="submitPost()"
                  />
                  <!-- <button
                    class="
                      w-full
                      bg-gray-100
                      rounded-xllareg
                      py-10
                      text-gray-200
                      font-bold
                    "
                  >
                    Post
                  </button> -->
                </div>
              </div>
            </div>
          </TransitionChild>
        </div>
      </div>
    </Dialog>
  </TransitionRoot>
</template>

<script lang="ts">
import { mapMutations, mapState } from "vuex";
import IgButton from "@/components/baseUI/IgButton.vue";
import { defineComponent } from "vue";
import axios from "axios";
import { emitter } from "@/main";
import {
  TransitionRoot,
  TransitionChild,
  Dialog,
  DialogOverlay,
} from "@headlessui/vue";
export default defineComponent({
  name: "AddPost",
  props: {
    WithdrawOpen: {
      type: Boolean,
      required: true,
    },
    ModalName: {
      type: String,
      required: true,
    },
    backgroundColor: {
      type: String,
      required: true,
    },
    fontBold: {
      type: String,
      required: true,
    },
  },

  data() {
    return {
      description: "",
      descriptionErrorMessage: "",
      imageErrorMessage: "",
      image: "",
      dataImage: "",
    };
  },
  components: {
    TransitionRoot,
    TransitionChild,
    Dialog,
    DialogOverlay,
    IgButton,
  },
  computed: {
    ...mapState(["apiUrl"]),
    ...mapState(["ADDPOST"]),
  },
  methods: {
    ...mapMutations(["TOGGLE_ADD_POST"]),
    AddPost() {
      this.TOGGLE_ADD_POST();
    },
    imageLoad(event) {
      if (event.target.files && event.target.files[0]) {
        this.image = event.target.files[0];
        var reader = new FileReader();

        reader.readAsDataURL(event.target.files[0]); // read file as data url

        reader.onload = (e) => {
          this.dataImage =
            e.target && e.target.result ? e.target.result.toString() : "";
        };
      }
    },
    removeImage() {
      this.image = "";
      this.dataImage = "";
    },
    async submitPost() {
      this.descriptionErrorMessage = "";
      if (this.description != "") {
        try {
          let fromData = new FormData();
          fromData.append("space_uuid", this.$route.params.id.toString());
          fromData.append("description", this.description);
          if (this.image != "") {
            fromData.append("image", this.image);
          }
          var response = await axios.post(
            this.apiUrl + "/space/" + this.$route.params.id + "/post",
            fromData,
            {
              headers: {
                "Content-Type": "multipart/form-data",
                authorization: "Bearer " + localStorage.getItem("access_token"),
              },
            }
          );
          if (response.data) {
            this.image = "";
            this.dataImage = "";
            this.description = "";
            emitter.emit("addPost", response.data);
            this.AddPost();
          }
        } catch (error) {
          if (error && error.response) {
            if (error.response.data) {
              if (error.response.data.description) {
                this.descriptionErrorMessage =
                  error.response.data.description[0];
              }
              if (error.response.data.image) {
                this.imageErrorMessage = error.response.data.image[0];
              }
            }
          }
        }
      } else {
        this.descriptionErrorMessage = "Description field is required";
      }
    },
  },
});
</script>

<style lang="scss" scoped>
.border-red {
  border: 1px solid #9e3c3c !important;
}
.ig-auth-btn {
  &.ig-blue {
    background-color: #4932b8;
    color: #ffffff;
  }
  &.ig-white {
    background-color: #ffffff;
    color: #121319;
  }
  &.ig-bold {
    font-weight: bold;
  }
  &.ig-custome {
    background-color: #654bde;
    color: #ffffff;
  }
}

textarea::placeholder {
  color: #4e5163;
}
.image-btn {
  position: absolute;
  z-index: 1;
  bottom: 1rem;
}
.file-input {
  width: 100px;
  height: 30px;
  position: relative;
  border-radius: 15px;
  border: 1px solid #3d3f4d;
  color: #3d3f4d;
}
.file-input::before {
