
import { defineComponent } from "vue";

export default defineComponent({
  name: "CreatePost",
  props: {
    data: {
      type: Object,
      required: true,
    },
  },
  data() {
    return {
      like: 0,
      dislike: 0,
    };
  },
  methods: {
    displayDateTime(value) {
      if (value == null || value == "") return "";

      return new Date(value).toLocaleString("en-GB", {
        day: "2-digit",
        month: "long",
        year: "numeric",
        minute: "2-digit",
        second: "2-digit",
      });
    },
  },
  created() {
    this.like = 0;
    this.dislike = 0;
    if (this.data.like_dislikes) {
      this.data.like_dislikes.forEach((element) => {
        if (element.like) this.like++;
        if (element.dislike) this.dislike++;
      });
    }
  },
});
