<template>
  <div class="grid md:grid-cols-10">
    <div class="flex flex-col grid-cols-10 md:col-span-7 md:mr-6">
      <div class="flex justify-between mb-30">
        <div class="serch-input">
          <input
            type="text"
            placeholder="Search tournament"
            v-model="keyword"
            @input="getSearchData()"
          />
          <a
            href="javascript:void(0)"
            v-if="keyword != ''"
            @click="cleanSearchData()"
          >
            <svg
              width="0.8rem"
              height="0.8rem"
              viewBox="0 0 16 16"
              fill="none"
              xmlns="http://www.w3.org/2000/svg"
            >
              <path
                d="M15.6548 1.40965L14.5581 0.325903C14.2613 0.029129 13.771 0.029129 13.4613 0.325903L7.99032 5.79674L2.41626 0.222677C2.11935 -0.0742258 1.62903 -0.0742258 1.31948 0.222677L0.222708 1.31945C-0.0741953 1.61623 -0.0741953 2.10655 0.222708 2.41623L5.784 7.97752L0.325934 13.4614C0.0291595 13.7582 0.0291595 14.2485 0.325934 14.5582L1.42271 15.6549C1.71948 15.9517 2.2098 15.9517 2.51948 15.6549L7.99032 10.184L13.4613 15.6549C13.7581 15.9517 14.2484 15.9517 14.5581 15.6549L15.6548 14.5582C15.9516 14.2614 15.9516 13.7711 15.6548 13.4614L10.171 7.99042L15.6419 2.51958C15.9516 2.20965 15.9516 1.71932 15.6548 1.40965Z"
                fill="white"
              />
            </svg>
          </a>
        </div>
        <!-- <search-post></search-post> -->
        <!-- <add-post></add-post> -->
      </div>
      <div class="flex flex-col">
        <AvailableTournamentHorizontal
          v-for="(item, index) in tournamentData"
          :key="index"
          :data="item"
        />
      </div>
    </div>
    <div class="grid-cols-10 md:col-span-3">
      <filter-choices></filter-choices>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent } from "vue";
// import SearchPost from "../posts/SearchPost.vue";
// import AddPost from "../posts/AddPost.vue";
import FilterChoices from "./FilterChoices.vue";
import axios from "axios";
import { mapState } from "vuex";
import AvailableTournamentHorizontal from "@/components/availableTournament/AvailableTournamentHorizontal.vue";

export default defineComponent({
  name: "Tournament",
  components: {
    // SearchPost,
    // AddPost,
    FilterChoices,
    AvailableTournamentHorizontal,
  },
  data() {
    return {
      keyword: "",

      tournamentPage: 1,
      tournamentTotal: 0,
      tournamentData: [],
    };
  },
  computed: {
    ...mapState(["apiUrl"]),
  },
  methods: {
    getSearchData() {
      if (this.keyword.length % 3 == 0) {
        this.getTournament(1);
      }
    },
    cleanSearchData() {
      this.keyword = "";
      this.getTournament(1);
    },
    async getTournament(page) {
      var response = await axios.get(this.apiUrl + "/tournament/", {
        params: {
          page: page,
          keyword: this.keyword,
        },
      });
      if (response.data != null && response.data.results) {
        this.tournamentPage = page;
        this.tournamentTotal = response.data.count;
        if (page == 1) this.tournamentData = [];
        this.tournamentData = this.tournamentData.concat(response.data.results);
      }
    },
  },
  async created() {
    this.getTournament(1);
  },
});
</script>

<style lang="scss" scoped>
.gradient-bg {
  background: radial-gradient(50% 50% at 50% 50%, #37b0fd 0%, #2a74e7 100%);
}
.serch-input {
  width: 18rem;
  position: relative;
  display: flex;
  input {
    width: 100%;
    border: 1px solid $borderColor;
    background: transparent !important;
    padding: 0.4rem 1rem;
    outline: none !important;
    border-radius: 10px;
    height: 2.5rem;
  }
  input::placeholder {
    color: #4e5163;
  }

  a {
    position: absolute;
    right: 0.75rem;
    top: 1rem;
  }
}
</style>
