    <div class="mt-6">
      <div class="w-100 text-center" v-if="applyFilter">
        <a href="javascript:void(0)" class="color-red" @click="cleanFilter()"
          >Reset Filter</a
        >
      </div>
      <div class="w-100" v-else-if="checkFilterApply">
        <IgButton
          class="ig-full px-10"
          buttonName="apply"
          textColor="ig-white-text"
          backgroundColor="ig-purple"
          @click="getFilterData()"
        />
      </div>
      <div class="w-100" v-else>
        <IgButton
          class="ig-full px-10"
