<script lang="ts">
import { defineComponent } from "vue";
import { Menu, MenuButton, MenuItems, MenuItem } from "@headlessui/vue";

// import {
//   Listbox,
//   ListboxButton,
//   ListboxOptions,
//   ListboxOption,
// } from "@headlessui/vue";
import IgDatePicker from "../baseUI/IgDatePicker.vue";
import IgButton from "../baseUI/IgButton.vue";
import { emitter } from "@/main";

export default defineComponent({
  props: ["filterApply"],
  data() {
    return {
      applyFilter: false,

      start_date: "",
      end_date: "",
      selectedPostTypeName: "",
      postTypeData: [
        { name: "Tournament activities", selected: false },
        { name: "Other activities", selected: false },
        { name: "Member post", selected: false },
      ],
    };
  },
  components: {
    Menu,
    MenuButton,
    MenuItems,
    MenuItem,
    // Listbox,
    IgButton,
    // ListboxButton,
    // ListboxOptions,
    // ListboxOption,
    IgDatePicker,
  },
  computed: {
    checkFilterApply() {
      if (
        this.start_date != "" ||
        this.end_date != "" ||
        this.selectedPostTypeName != ""
      ) {
        return true;
      }
      return false;
    },
  },
  methods: {
    selectPostType(index) {
      this.postTypeData.forEach((element) => {
        element.selected = false;
      });
      this.postTypeData[index].selected = true;
      this.selectedPostTypeName = this.postTypeData[index].name;
    },
    changeStartDate(date) {
      this.start_date = date;
    },
    changeEndDate(date) {
      this.end_date = date;
    },
    getFilterData() {
      this.applyFilter = true;
      this.$emit(
        "filterApply",
        this.start_date,
        this.end_date,
        this.selectedPostTypeName
      );
    },
    cleanFilter() {
      this.applyFilter = false;
      this.start_date = "";
      this.end_date = "";
      this.selectedPostTypeName = "";
      this.postTypeData.forEach((element) => {
        element.selected = false;
      });
      emitter.emit("cleanDatePickerDate");
      this.$emit(
        "filterApply",
        this.start_date,
        this.end_date,
        this.selectedPostTypeName
      );
    },
  },
});
</script>

<style lang="scss" scoped>
