  &-leave-active {
    transition: all 0.4s ease-in-out;
  }
  &-enter-from {
    height: 5rem;
    opacity: 0;
  }

  &-enter-to {
    opacity: 1;
  }
}
.placeholder {
  color: #4e5163;
}
