}
.select-input {
  width: 100%;
  border: 1px solid $borderColor;
  background: transparent !important;
  padding: 0.4rem 1rem;
  outline: none !important;
  border-radius: 10px;
  display: flex;
}
.post-type-block {
  min-width: 17.5rem;
  background-color: #21212b;
}
</style>
