<template>
  <div
    class="
      p-16
      md:p-30
      w-full
      lg:w-1/55
      bg-black-default
      rounded-xl
      mb-12
      lg:mb-0
    "
  >
    <h4 class="text-base text-white">
      {{ $t("space." + descName + "") }}
    </h4>
    <p class="text-gray-200 mt-16">
      {{ descInfo }}
    </p>
    <p class="text-xs text-white mt-16 mb-20 flex">
      <span role="img" class="mr-3">
        <img :src="dateIcon" alt="date icon" />
      </span>
      <span>
        {{ $t("space.creted_on") + " " + displayDate(descDate) }}
      </span>
    </p>
    <div v-if="spaceMedia.length > 0">
      <h5 class="text-base text-white mb-4 mt-8">
        {{ $t("space.social_media") }}
      </h5>
      <div class="flex">
        <div v-for="(item, index) in spaceMedia" :key="index">
          <a :href="item.link" target="_blank"
            ><div
              class="media-icon"
              :style="{ backgroundImage: 'url(' + item.icon + ')' }"
            ></div
          ></a>
        </div>
      </div>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent } from "vue";

export default defineComponent({
  name: "Description",
  props: {
    descName: {
      type: String,
      required: true,
    },
    descInfo: {
      type: String,
      required: true,
    },
    descDate: {
      type: String,
      required: true,
    },
    spaceMedia: {
      type: Array,
      required: true,
    },
    dateIcon: {
      type: String,
      required: true,
    },
  },
  methods: {
    displayDate(value) {
      if (value == null || value == "") return "";

      return new Date(value).toLocaleString("en-GB", {
        day: "2-digit",
        month: "long",
        year: "numeric",
      });
    },
  },
});
</script>
<style lang="scss" scoped>
.media-icon {
  width: 2.5rem;
  height: 2.5rem;
  background-position: center;
  background-size: cover;
  border-radius: 50%;
  background-repeat: no-repeat;
  // border: 1px solid $borderColor;
}
</style>
