<script lang="ts">
import { defineComponent } from "vue";
import { Menu, MenuButton, MenuItems, MenuItem } from "@headlessui/vue";
import IgButton from "@/components/baseUI/IgButton.vue";

export default defineComponent({
  data() {
    return {
      selectedStatusName: "",
      statusData: [
        { name: "Upcoming", selected: false },
        { name: "Ongoing", selected: false },
        { name: "Previous", selected: false },
      ],

      startDate: "",
      endDate: "",

      selectedRegionName: "",
      regionData: [
        { name: "India", selected: false },
        { name: "USA", selected: false },
      ],

      selectedTeamSizeName: "",
      teamSizeData: [
        { name: "5 Member", selected: false },
        { name: "10 Member", selected: false },
        { name: "15 Member", selected: false },
      ],
      min_fee: "",
      max_fee: "",

      applyFilter: false,
    };
  },
  components: {
    Menu,
    MenuButton,
    MenuItems,
    MenuItem,
    IgButton,
  },
  computed: {
    checkFilterApply() {
      if (
        this.selectedStatusName != "" ||
        this.selectedRegionName != "" ||
        this.selectedTeamSizeName != ""
      ) {
        return true;
      }
      return false;
    },
  },
  methods: {
    selectStatus(index) {
      this.statusData.forEach((element) => {
        element.selected = false;
      });
      this.statusData[index].selected = true;
      this.selectedStatusName = this.statusData[index].name;
    },
    selectRegion(index) {
      this.regionData.forEach((element) => {
        element.selected = false;
      });
      this.regionData[index].selected = true;
      this.selectedRegionName = this.regionData[index].name;
    },
    selectTeamSize(index) {
      this.teamSizeData.forEach((element) => {
        element.selected = false;
      });
      this.teamSizeData[index].selected = true;
      this.selectedTeamSizeName = this.teamSizeData[index].name;
    },
    cleanFilter() {
      this.applyFilter = false;
      this.selectedStatusName = "";
      this.statusData.forEach((element) => {
        element.selected = false;
      });
      this.selectedRegionName = "";
      this.regionData.forEach((element) => {
        element.selected = false;
      });
      this.selectedTeamSizeName = "";
      this.teamSizeData.forEach((element) => {
        element.selected = false;
      });
      this.min_fee = "";
      this.max_fee = "";
    },
  },
});
</script>
<style lang="scss" scoped>
input {
  width: 100%;
  border: 1px solid $borderColor;
  background: transparent !important;
  padding: 0.4rem 1rem;
  outline: none !important;
  border-radius: 10px;
}
.select-input {
  width: 100%;
  border: 1px solid $borderColor;
  background: transparent !important;
  padding: 0.4rem 1rem;
  outline: none !important;
  border-radius: 10px;
  display: flex;
}
.status-dropdown {
  min-width: 21rem;
  padding: 0.8rem 1.2rem;
  z-index: 1;
}
.region-dropdown {
  min-width: 17.5rem;
  padding: 0.8rem 1.2rem;
  z-index: 1;
}
.fee-range-input {
  position: relative;
  input {
    padding-left: 1.8rem !important;
  }
}
.fee-range-input::before {
  position: absolute;
  content: "$";
  line-height: 2.4rem;
  left: 0rem;
  width: 2rem;
  text-align: center;
}
.color-red {
  color: #c53f3f !important;
}
</style>
