<template>
  <div class="grid grid-cols-10">
    <div class="flex flex-col justify-between col-10 md:col-span-7 md:mr-10">
      <div class="flex justify-between mb-10">
        <div class="serch-input">
          <input
            type="text"
            placeholder="Search postings"
            v-model="keyword"
            @input="getSearchData()"
          />
          <a
            href="javascript:void(0)"
            v-if="keyword != ''"
            @click="cleanSearchData()"
          >
            <svg
              width="0.8rem"
              height="0.8rem"
              viewBox="0 0 16 16"
              fill="none"
              xmlns="http://www.w3.org/2000/svg"
            >
              <path
                d="M15.6548 1.40965L14.5581 0.325903C14.2613 0.029129 13.771 0.029129 13.4613 0.325903L7.99032 5.79674L2.41626 0.222677C2.11935 -0.0742258 1.62903 -0.0742258 1.31948 0.222677L0.222708 1.31945C-0.0741953 1.61623 -0.0741953 2.10655 0.222708 2.41623L5.784 7.97752L0.325934 13.4614C0.0291595 13.7582 0.0291595 14.2485 0.325934 14.5582L1.42271 15.6549C1.71948 15.9517 2.2098 15.9517 2.51948 15.6549L7.99032 10.184L13.4613 15.6549C13.7581 15.9517 14.2484 15.9517 14.5581 15.6549L15.6548 14.5582C15.9516 14.2614 15.9516 13.7711 15.6548 13.4614L10.171 7.99042L15.6419 2.51958C15.9516 2.20965 15.9516 1.71932 15.6548 1.40965Z"
                fill="white"
              />
            </svg>
          </a>
        </div>
        <!-- <search-post></search-post> -->
        <add-post></add-post>
      </div>
      <div v-if="postData.length > 0">
        <post v-for="(item, index) in postData" :key="index" :data="item">
        </post>
      </div>
      <div v-else class="text-center">
        <img
          src="@/assets/images/no-data-found.png"
          class="mx-auto"
          style="width: 20rem"
        />
        <p class="mt-3">
          <b>{{ $t("tournamentMenu.your_search_cant_be_found") }}</b>
        </p>
        <small class="color-gray">{{
          $t(
            "tournamentMenu.make_sure_your_keyword_is_correct_or_try_another_keyword"
          )
        }}</small>
      </div>
    </div>
    <go-to-date
      @filterApply="filterApply"
      class="col-10 md:col-span-3"
    ></go-to-date>
  </div>
</template>

<script lang="ts">
import { defineComponent } from "vue";
import Post from "./posts/Post.vue";
// import SearchPost from "./posts/SearchPost.vue";
import AddPost from "./posts/AddPost.vue";
import GoToDate from "./GoToDate.vue";
import axios from "axios";
import { mapState } from "vuex";
import { emitter } from "@/main";
export default defineComponent({
  name: "Postings",
  data() {
    return {
      keyword: "",
      startDate: "",
      endDate: "",
      postType: "",

      postPage: 1,
      postTotal: 0,
      postData: [],
    };
  },
  components: {
    Post,
    // SearchPost,
    AddPost,
    GoToDate,
  },
  computed: {
    ...mapState(["apiUrl"]),
  },
  methods: {
    formatDate(date) {
      if (!date || date == "") return "";
      var d = new Date(date),
        month = "" + (d.getMonth() + 1),
        day = "" + d.getDate(),
        year = d.getFullYear();

      if (month.length < 2) month = "0" + month;
      if (day.length < 2) day = "0" + day;

      return [year, month, day].join("-");
    },
    async getPosts(page) {
      var response = await axios.get(
        this.apiUrl + "/space/" + this.$route.params.id + "/post",
        {
          params: {
            page: page,
            keyword: this.keyword,
            start_date: this.formatDate(this.startDate),
            end_date: this.formatDate(this.endDate),
          },
          headers: {
            authorization: "Bearer " + localStorage.getItem("access_token"),
          },
        }
      );
      if (response.data != null) {
        this.postPage = page;
        this.postTotal = response.data.count;
        if (page == 1) this.postData = [];
        this.postData = this.postData.concat(response.data);
      }
    },
    getSearchData() {
      if (this.keyword.length % 3 == 0) {
        this.getPosts(1);
      }
    },
    cleanSearchData() {
      this.keyword = "";
      this.getPosts(1);
    },
    filterApply(startDate, endDate, postType) {
      this.startDate = startDate;
      this.endDate = endDate;
      this.postType = postType;
      this.getPosts(1);
    },
  },
  async created() {
    this.getPosts(1);
    emitter.on("addPost", () => {
      this.getPosts(1);
    });
  },
  deactivated() {
    emitter.off("addPost");
  },
});
</script>
<style lang="scss" scoped>
.serch-input {
  width: 18rem;
  position: relative;
  display: flex;
  input {
    width: 100%;
    border: 1px solid $borderColor;
    background: transparent !important;
    padding: 0.4rem 1rem;
    outline: none !important;
    border-radius: 10px;
    height: 2.5rem;
  }
  input::placeholder {
    color: #4e5163;
  }

  a {
    position: absolute;
    right: 0.75rem;
    top: 1rem;
  }
}
</style>
